<template>
  <div>
    <!-- navBar -->
    <van-nav-bar title="黑马头条 - 登录" />
    <!-- 表单 -->
    <van-form @submit="onSubmit">
      <van-field
        v-model="user.mobile"
        required
        name="mobile"
        label="手机号"
        placeholder="手机号"
        :rules="[{ required: true, message: '请填写11位手机号', pattern: /1[3-9]\d{9}/ }]"
      />
      <van-field
        v-model="user.code"
        required
        type="password"
        name="code"
        label="密码"
        placeholder="密码"
        :rules="[{ required: true, message: '请填写6位密码', pattern: /\d{6}/ }]"
      />
      <div style="margin: 16px">
        <!--  -->
        <van-button
          round
          block
          type="info"
          native-type="submit"
          :loading="loading"
          loading-text="正在登录ing..."
          :disabled="loading"
          >登录</van-button
        >
      </div>
    </van-form>
  </div>
</template>

<script>
import { Toast } from 'vant'
import { setToken } from '@/utils/token'
export default {
  name: 'Login',
  data() {
    return {
      user: {
        mobile: '18896656627', // 手机号
        code: '246810' // 验证码(密码, 必须是'246810')
      },
      loading: false // 控制登录按钮显示加载状态
    }
  },
  methods: {
    // 登录
    async onSubmit(values) {
      // 参数values: 收集到的表单数据
      // 加载
      this.loading = true
      try {
        const res = await this.$API.reqLogin(values)
        // 存储token
        setToken(res.data.data.token)
        // 存储refresh_token
        localStorage.setItem('refresh_token', res.data.data.refresh_token)
        // 路由跳转--> Layout
        // 必须写在最后
        this.$router.replace(this.$route.query.path || '/layout/home')
        Toast.success('登录成功')
      } catch (err) {
        Toast.fail('账号或密码错误')
      }
      // 关闭加载
      this.loading = false
    }
  }
}
</script>

<style lang="less" scoped></style>
